import { Component, Inject } from '@angular/core';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';

import { MemberModel } from './../models/member.model';

import { WEBAPISERVICE, IWebApiServices, IDataStructure, IEmptyConstruct, IFilter, IResultMessage, ResultStatus } from './../../core/index';
import { AngularFire, FirebaseListObservable } from 'angularfire2';

@Injectable()
export class MemberServices {

    public entity: MemberModel = new MemberModel();

    constructor( @Inject(WEBAPISERVICE) public webApiServices: IWebApiServices) { }

    public updateMember(entity:any) {
        let that = this;
        that.webApiServices.updateEntityAsync(MemberModel, entity).then((result: IResultMessage) => {
            if (result.resultStatus == ResultStatus.Successful)
                result.dataResult.subscribe((res: any) => {
                    that.entity = res;
                });
        });
    }
}


