import { Component, Inject } from '@angular/core';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';

import { ConfigModel } from './../models/config.model';

import { WEBAPISERVICE, IWebApiServices, IDataStructure, IEmptyConstruct, IFilter, IResultMessage, ResultStatus } from './../../core/index';
import { AngularFire, FirebaseListObservable } from 'angularfire2';

@Injectable()
export class ConfigServices {

    public entity: ConfigModel = new ConfigModel();

    constructor( @Inject(WEBAPISERVICE) public webApiServices: IWebApiServices) { }

    public loadConfig() {
        let that = this;
        setTimeout(_ => {
            that.webApiServices.getSingleAsync(ConfigModel, 'b806bb31-ab9b-44d7-a111-f359a8260a48').then((result: IResultMessage) => {
                if (result.resultStatus == ResultStatus.Successful)
                    result.dataResult.subscribe((res: any) => {
                        that.entity = res;
                    });
            });
        }, 1);
    }

    public updateConfig(totalAmountOld: number, totalAmountNew: number) {
        let that = this;
        that.entity.TotalAmount -= totalAmountOld;
        that.entity.TotalAmount += totalAmountNew;
        that.webApiServices.updateEntityAsync(ConfigModel, that.entity).then((result: IResultMessage) => {
            if (result.resultStatus == ResultStatus.Successful)
                result.dataResult.subscribe((res: any) => {
                    that.entity = res;
                });
        });
    }
}


