import { Component, AfterViewInit, OnInit, NgZone, Inject, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { MemberRefundModel } from './../../../models/member-refund.model';

import {
    IEmptyConstruct, IDataStructure, IGridButton, ButtonActionType,
    IWebApiServices, IResultMessage, ResultStatus, NotificationService,
    GridViewControl, PaginatorControl, IFilter, OperatorType, WEBAPISERVICE
} from './../../../../core/index';

import { MemberServices } from './../../../services/index';

@Component({
    selector: "member-refund-list",
    templateUrl: `./member-refund-list.component.html`
})

export class MemberRefundListComponent implements OnInit {

    @ViewChild("grid") grid: GridViewControl;
    private entityType: IEmptyConstruct = MemberRefundModel;
    private entity: MemberRefundModel = new MemberRefundModel();
    private filters: IFilter[] = [];

    constructor(private router: Router,
        private memberServices: MemberServices,
        @Inject(WEBAPISERVICE) private webApiServices: IWebApiServices,
        @Inject(NotificationService) private notificationService: NotificationService) {

        this.filters.push({ fieldName: 'RefundMonth1', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
        this.filters.push({ fieldName: 'RefundMonth2', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
        this.filters.push({ fieldName: 'RefundMonth3', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
    }

    ngOnInit() {
    }

    private onActionEvent(event: any) {
        let that = this;
        var item = <IDataStructure>event.data;
        var button = <IGridButton>event.action;
        switch (button.Action) {
            case ButtonActionType.Edit:
               that.onEditEntity(button, <MemberRefundModel>item);
                break;
        }
    }

    private onEditEntity(button: IGridButton, entity: MemberRefundModel) {
        entity.RefundAmount += 1000;
        this.memberServices.updateMember(entity);
        this.grid.loadData();
    }

    private onLoadDataEvent(data: any) {
        let that = this;
        that.filters = [];
        that.filters.push({ fieldName: 'RefundMonth1', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
        that.filters.push({ fieldName: 'RefundMonth2', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
        that.filters.push({ fieldName: 'RefundMonth3', operator: OperatorType.CONTAINS, value: new Date().getMonth() + 1 });
        that.grid.loadData();
    }

    private updateConfig(entity: any) {
    }
}


